







































  html,body,div,h1,h2,h3,h4,ul,li,p {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_1472109320_852638.eot'); /* IE9*/
  src: url('//at.alicdn.com/t/font_1472109320_852638.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('//at.alicdn.com/t/font_1472109320_852638.woff') format('woff'), /* chrome、firefox */
  url('//at.alicdn.com/t/font_1472109320_852638.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url('//at.alicdn.com/t/font_1472109320_852638.svg#iconfont') format('svg'); /* iOS 4.1- */
}
                    
                    
  .iconfont{
    font-family:"iconfont";
     font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    -moz-osx-font-smoothing: grayscale;
}

  html {
    height: 100%;
  }

  body {
    height: 100%;
  }

  #app {
    color: #2c3e50;
    font-size: .25rem;
    font-family: Source Sans Pro, Helvetica, sans-serif;
  }

  .wrap {
    position: relative;
    overflow: hidden;
  }

  li {
    cursor: pointer;
  }

  .isFinished {
    color: #f40;
  }

  #app a {
    color: #622A1D;
    text-decoration: none;
  }

  .logo {
    width: 100px;
    height: 100px
  }

  .tran-top {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  a.btn:active {
    opacity: .2;
  }



